import React, {useState} from 'react';
import { Button, Form, Input, message } from 'antd';
import axios from 'axios';

const App = () => {
  const [loading, setLoading] = useState(false);
  const onFinish = async (values) => {
    setLoading(true);
    axios({
      method: "POST",
      url: "https://formspree.io/f/xqkopbnk",
      data: values
    }).then((res) => {
      message.success('');
      setLoading(false);
    }).catch(() => {
      setLoading(false);
    }) 
  };
  const onFinishFailed = (errorInfo) => {
    console.log('Failed:', errorInfo);
  };
  return (
    <div className='content-us'>
      <h1 name="title" className="title-h1"></h1>
      <Form
        name="basic"
        labelCol={{
          span: 8,
        }}
        wrapperCol={{
          span: 16,
        }}
        initialValues={{
          remember: true,
        }}
        onFinish={onFinish}
        onFinishFailed={onFinishFailed}
        autoComplete="off"
      >
        <Form.Item
          label=""
          name="username"
          rules={[
            {
              required: true,
              message: '',
            },
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          label=""
          name="email"
          rules={[
            {
              required: true,
              message: '',
            },
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          label=""
          name="message"
          rules={[
            {
              message: '',
            },
          ]}
        >
          <Input.TextArea />
        </Form.Item>
        <Form.Item
          wrapperCol={{
            offset: 8,
            span: 16,
          }}
        >
          <Button type="primary" htmlType="submit" loading={loading}>
            
          </Button>
        </Form.Item>
      </Form>
    </div>
    
  );
};
export default App;

