/* eslint arrow-parens: 0 */
import React from 'react';
import { enquireScreen } from 'enquire-js';

import Banner5 from './Banner5';
import Content3 from './Content3';
// import Content0 from './Content0';
import Content5 from './Content5';
import Content4 from './Content4';
import Feature8 from './Feature8';
// import Teams2 from './Teams2';
import Footer0 from './Footer0';
import Nav0 from './Nav0';
import NormalLoginForm from './ContentUs'

import {
  Nav00DataSource,
  Banner50DataSource,
  Content30DataSource,
  // Content00DataSource,
  Content50DataSource,
  Content40DataSource,
  Feature80DataSource,
  // Teams20DataSource,
  Footer01DataSource,
} from './data.source';
import './less/antMotionStyle.less';

let isMobile;
enquireScreen((b) => {
  isMobile = b;
});

const { location = {} } = typeof window !== 'undefined' ? window : {};

export default class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isMobile,
      show: !location.port, //  dva 2.0 
    };
  }

  componentDidMount() {
    // ;
    enquireScreen((b) => {
      this.setState({ isMobile: !!b });
    });
    // dva 2.0 
    /*  dva 2.0  start */
    if (location.port) {
      //  build  200-300ms ;
      setTimeout(() => {
        this.setState({
          show: true,
        });
      }, 500);
    }
    /*  dva 2.0  end */
  }

  render() {
    const children = [
      <Nav0
        id="Nav0_0"
        key="Nav0_0"
        dataSource={Nav00DataSource}
        isMobile={this.state.isMobile}
      />,
      <Banner5
        id="Banner5_0"
        key="Banner5_0"
        dataSource={Banner50DataSource}
        isMobile={this.state.isMobile}
      />,
      <NormalLoginForm key="NormalLoginForm0_0" />,
      <Content5
        id="Content5_0"
        key="Content5_0"
        dataSource={Content50DataSource}
        isMobile={this.state.isMobile}
      />,
      <Content3
        id="Content3_0"
        key="Content3_0"
        dataSource={Content30DataSource}
        isMobile={this.state.isMobile}
      />,
      // <Content0
      //   id="Content0_0"
      //   key="Content0_0"
      //   dataSource={Content00DataSource}
      //   isMobile={this.state.isMobile}
      // />,
      <Feature8
        id="Feature8_0"
        key="Feature8_0"
        dataSource={Feature80DataSource}
        isMobile={this.state.isMobile}
      />,
      <Content4
        id="Content4_0"
        key="Content4_0"
        dataSource={Content40DataSource}
        isMobile={this.state.isMobile}
      />,
      // <Teams2
      //   id="Teams2_0"
      //   key="Teams2_0"
      //   dataSource={Teams20DataSource}
      //   isMobile={this.state.isMobile}
      // />,
      <Footer0
        id="Footer0_1"
        key="Footer0_1"
        dataSource={Footer01DataSource}
        isMobile={this.state.isMobile}
      />,
    ];
    return (
      <div
        className="templates-wrapper"
        ref={(d) => {
          this.dom = d;
        }}
      >
        {/*  dva 2.0  {children} start */}
        {this.state.show && children}
        {/*  dva 2.0  {children} end */}
      </div>
    );
  }
}
